@font-face {
  font-family: "robotobold";
  src: url("../fonts/roboto-bold-webfont.woff2") format("woff2"), url("../fonts/roboto-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "robotothin";
  src: url("../fonts/roboto-thin-webfont.woff2") format("woff2"), url("../fonts/roboto-thin-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
.ns-menu__item {
  text-decoration: none;
  text-transform: uppercase;
  font-family: "robotobold", sans-serif;
  font-size: 1.25rem;
}

.ns-menu {
  position: relative;
  min-height: 100vh;
  width: 100%;
  overflow: hidden;
}
.ns-menu__box {
  display: flex;
  justify-content: space-between;
  max-width: 75rem;
  margin: auto;
  flex-direction: column;
  transition: all 400ms;
  position: relative;
  right: -100%;
  top: -500px;
}
@media only screen and (min-width: 576px) {
  .ns-menu__box {
    flex-direction: row;
    align-items: center;
    position: static;
  }
}
.ns-menu__item {
  text-align: center;
  padding: 0.625rem;
  transition: all 400ms;
}
.ns-lines {
  height: 1.625rem;
  width: 2rem;
  position: absolute;
  top: 1.25rem;
  right: 1.25rem;
  z-index: 10;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media only screen and (min-width: 576px) {
  .ns-lines {
    display: none;
  }
}
.ns-lines__line {
  display: block;
  height: 0.25rem;
  width: 100%;
  border-radius: 0.625rem;
  background: black;
}
.ns-lines__line--v1 {
  transform-origin: 0 0;
  transition: transform 0.4s ease-in-out;
}
.ns-lines__line--v2 {
  transition: transform 0.2s ease-in-out;
}
.ns-lines__line--v3 {
  transform-origin: 0 100%;
  transition: transform 0.4s ease-in-out;
}

.ns-menu__click {
  position: absolute;
  height: 2rem;
  width: 2rem;
  top: 1.25rem;
  right: 1.25rem;
  z-index: 20;
  cursor: pointer;
  opacity: 0;
}
.ns-menu__click:checked + .ns-lines .ns-lines__line--v1 {
  transform: rotate(45deg);
}
.ns-menu__click:checked + .ns-lines .ns-lines__line--v2 {
  transform: scaleY(0);
}
.ns-menu__click:checked + .ns-lines .ns-lines__line--v3 {
  transform: rotate(-45deg);
}
.ns-menu__click:checked + .ns-lines + .ns-menu__box {
  right: 0;
  top: 0;
}